
import { defineComponent, onMounted, ref } from "vue";
import { useRouter, useRoute } from "vue-router";
import BannerService from "@/store/modules/banner/service/banner.service";
import IBanner from "@/store/modules/banner/interface/banner.interface";
import Swal from "sweetalert2/dist/sweetalert2.js";
import draggable from 'vuedraggable'
import { createToaster } from "@meforma/vue-toaster";
import * as animationHelper from "@/store/helpers/animation.helper";
import * as validationHelper from "@/store/helpers/validation.helper";

export default defineComponent({
  name: "Banners",
  components: {
	  draggable
  },
  setup() {
    	const router = useRouter();
		const bannerService = new BannerService();
		const banners = ref<Array<any>>([]);
		const show = ref<Boolean>(false);
		const toaster = createToaster({ position: "bottom-left" });
		const banner = ref<IBanner>({
			id: null,
			name: '',
			file: null,
			concessionary_id: null,
			site_id: null,
			duration: 0,
			visible: false
		});

		const getData = () => bannerService.getData()
		.then((response) => {
			response.data.forEach((element: any) => {
				element.visible = (element.visible == 1)
			});
			banners.value = response.data
		})
		.catch((error) => {
			
		});

		const saveStatus = (banner: any) => bannerService.changeStatus(banner.id, banner)
        .then((response) => {
            Swal.fire(
            'Confirmación!',
            'Información actualizada',
            'success'
            )
        })
        .catch((error) => {
            Swal.fire(
            'Oh no!',
            'No se guardaron los cambios',
            'error'
            )
        });

		const changeValue = (e: any, banner: any) => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea cambiar el estado?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Guardar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						saveStatus(banner);
					}
					else{
						banners.value.forEach((element: any, index: any) => {
							if(element.id == banner.id){
								banners.value[index].visible = !banner.visible;
							}
						});
					}
			})
		}

		const changeOrder = ()=>{
			banners.value.forEach((banner: any, index: any) => {
				banners.value[index].order = index + 1
			})
			saveOrder(getIdsOrder())
		}

		const getIdsOrder = ()=>{
			let idsOrder: any[] = []
			banners.value.forEach((banner: any, index: any) => {
				idsOrder.push({id: banner.id, order: banner.order})
			})
			return idsOrder
		}

		const saveOrder = (idsOrder)=>{
			bannerService.updateOrder({data: idsOrder})
			.then((response) => {
				toaster.success(`Orden actualizo`);
			})
			.catch((error) => {
				toaster.error(`No se guardaron los cambios`);
			});
		}

		const editAnimation = (bannerData: any) => {
			banner.value = bannerData;
		}

		const validateAnimation = () => {
			let disabled = false;
			if(!banner.value.id || !banner.value.duration || isNaN(banner.value.duration)) disabled = true;
			return disabled;
		}

		const saveAnimation = () => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea cambiar los cambios?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Guardar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						bannerService.saveAnimation(banner.value.id, banner.value)
						.then((response) => {
							Swal.fire(
							'Confirmación!',
							'Información actualizada',
							'success'
							)
							.then((result) => {
								getData()
								document.getElementById('animation-close-button')?.click()
							});
							
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se guardaron los cambios',
							'error'
							)
						});
					}
			})
		}

		const deleteData = (bannerId) => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea eliminar el registro seleccionado?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Eliminar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						bannerService.destroy(bannerId)
						.then((response) => {
							Swal.fire(
							'Confirmación!',
							'Información eliminada exitosamente',
							'success'
							).then(result => {
								getData()
							});
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se completo la operación',
							'error'
							);
						});
					}
			})
			
		}

		onMounted(() => {
			getData()
		});

		return {
			banners,
			changeValue,
			changeOrder,
			animationHelper,
			editAnimation,
			validateAnimation,
			saveAnimation,
			banner,
			deleteData
		};
  },
});
