import ApiService from "@/core/services/ApiService";
import axios from 'axios';
import {httpOptionsFile, httpOptions} from '../../global';

const apiServie = new ApiService();

export default class BannerService{
  getData() {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/banner`, httpOptions());
  }

  edit(id: any) {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/banner/${id}`, httpOptions());
  }

  create(data: any) {
    return axios.post(`${ApiService.vueInstance.axios.defaults.baseURL}/banner`, data, httpOptionsFile());
  }

  update(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/banner/${id}`, data, httpOptionsFile());
  }

  saveAnimation(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/banner/${id}/animation`, data, httpOptions());
  }

  updateOrder(data: any) {
    return axios.post(`${ApiService.vueInstance.axios.defaults.baseURL}/banner/update-order`, data, httpOptions());
  }

  destroy(id:any) {
    return axios.delete(`${ApiService.vueInstance.axios.defaults.baseURL}/banner/${id}`, httpOptions());
  }

  changeStatus(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/banner/${id}/status`, data, httpOptions());
  }
}
